import React from 'react'
import { useOutletContext } from 'react-router-dom'


function Contact() {
    const obj = useOutletContext()
    return (
        <div>
            <div>Contacts, {obj.book.length}</div>
            <ul className='text-blue-800 text-normal'>

                {obj.book.map(function (ele) { return <li>{ele}</li> })}
            </ul>
        </div>

    )
}

export default Contact