import { HashRouter, Routes, Route } from "react-router-dom"
import Home from "./pages/Home"
import Clients from "./pages/Clients"
import Contact from "./pages/Contact"
import About from "./pages/About"
import Layout from "./Components/Layout"
import MechLayout from "./Components/MechLayout"
import Phys1Home from "./pages/Phys_1/Phys1Home"
import Phys11 from "./pages/Phys_1/Phys11"
import Phys12 from "./pages/Phys_1/Phys12"
import Phys13 from "./pages/Phys_1/Phys13"

import EMLayout from "./Components/EMLayout"
import Phys2Home from "./pages/Phys_2/Phys2Home"
import Phys21 from "./pages/Phys_2/Phys21"
import Phys22 from "./pages/Phys_2/Phys22"
import Phys23 from "./pages/Phys_2/Phys23"

import ModLayout from "./Components/ModLayout"
import Phys3Home from "./pages/Phys_3/Phys3Home"
import Phys31 from "./pages/Phys_3/Phys31"
import Phys32 from "./pages/Phys_3/Phys32"
import Phys33 from "./pages/Phys_3/Phys33"

import Math1 from "./pages/Math_1/Math1"

import NotFound from "./pages/NotFound"




function App() {
  return (
    <div className="App">
      <HashRouter>
        <Routes>

          <Route path="/" element={<Layout />} >
            <Route index element={<Home />} />
            <Route path="about" element={<About />} />
            <Route path="contact" element={<Contact />} />
            <Route path="clients" element={<Clients />} />
          </Route>

          <Route path="/mec/" element={<MechLayout />}>
            <Route index element={<Phys1Home />} />
            <Route path="phys11" element={<Phys11 />} />
            <Route path="phys12/:code" element={<Phys12 />} />
            <Route path="phys13" element={<Phys13 />} />
          </Route>

          <Route path="/em/" element={<EMLayout />}>
            <Route index element={<Phys2Home />} />
            <Route path="phys21" element={<Phys21 />} />
            <Route path="phys22" element={<Phys22 />} />
            <Route path="phys23" element={<Phys23 />} />
          </Route>

          <Route path="/mod/" element={<ModLayout />}>
            <Route index element={<Phys3Home />} />
            <Route path="phys31" element={<Phys31 />} />
            <Route path="phys32" element={<Phys32 />} />
            <Route path="phys33" element={<Phys33 />} />
          </Route>

          <Route path="/math/*" element={<Math1 />}>


          </Route>



          <Route path="*" element={<NotFound />} />

        </Routes>


      </HashRouter>
    </div>
  );
}

export default App;
